import { Component }  from '@angular/core';
import { IonicPage, NavController, MenuController, NavParams, LoadingController, Platform, AlertController  }  from 'ionic-angular';
import { Developments, Cities ,Api }  from '../../providers';
 
@IonicPage() 
@Component({
  selector: 'page-developments',
  templateUrl: 'developments.html' 
})
export class DevelopmentsPage {
  cardItems   : any     = [];
  btnDetail   : boolean = false;

  public items: any = [];
  public type : any ;

  constructor(public loadingCtrl: LoadingController, public api : Api, public cities : Cities,   public NavParams: NavParams,public developments: Developments, public menu: MenuController, private navCtrl: NavController) {
    this.type = this.NavParams.data.length > 0 ? this.NavParams.data : false ;
    console.log('constructor')

  }
 
  ionViewDidEnter() {
    console.log('ionViewDidEnter',this.type )

    this.loadDevelopments(this.type);
    this.initializeItems();
    this.menu.enable(true);
  }

  initializeItems() {
    console.log('initializeItems',this.type )

    this.items = this.cardItems;
  }

  getItems(ev: any) {
    console.log('getItems',this.type )

    this.initializeItems();
    const val = ev.target.value;

    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        //return (item.developmentNameEs.toLowerCase().indexOf(val.toLowerCase()) > -1);
      
        if (item.developmentNameEs.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;
        } else if (item.folio.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;
        } else if (item.city.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        }


      
      })
    }

  }

  
  detail(id){

    console.log('detail',id)

    let loading = this.loadingCtrl.create({
      content: `
        <div class="custom-spinner-container" style="background-color: black;">
          <div class="custom-spinner-box" style="background-color: black;"></div>
        </div>`
    });

    loading.present(); 

  

    this.developments.query(id).subscribe(data => {

      this.cities.flag = 1;
      this.developments.development = data.json().data;
      this.navCtrl.push('DevelopmentDetailPage', id);
      this.btnDetail = false;
      loading.dismiss();


    });
 
 
  }

  loadDevelopments(type){
    console.log('loadDevelopments',type)

    this.cardItems  = [];
    var $this       = this; 

    this.developments.developments.forEach((value, key, index) => {
        
      var integer = 0;
      var integer2 = 0;

      if (value.description.length > 130) {          
        value.description = value.description.substring(0, 145);
        value.description = value.description + '...';   
      }

      //Tipo de moneda
      integer   = parseInt(this.api.filtreDevelopments.moneda, 10);
      integer2  = parseInt(value.average_currencyid , 10);

      if(this.api.filtreDevelopments.moneda){
        if( !( integer2 == integer) ){
          return;
        }
      }
      //-



      if(this.api.filtreDevelopments.state){
        ////console.log('Estado');
        ////console.log(value.stateid , '--', this.api.filtreDevelopments.state );

        if( !( value.stateid == this.api.filtreDevelopments.state) ){
          return;
        }

      }

      if(this.api.filtreDevelopments.township){
        ////console.log('municipio');
        ////console.log(value.idmunicipio , '--', this.api.filtreDevelopments.township );

        if( !( value.idmunicipio == this.api.filtreDevelopments.township) ){
          return;
        }

      }

      if(this.api.filtreDevelopments.suburb){
        ////console.log('colonia');
        ////console.log(value.idcolonia , '--', this.api.filtreDevelopments.suburb );

        if( !( value.idcolonia == this.api.filtreDevelopments.suburb) ){
          return;
        }

      }







      //Precio minimo
      integer   = parseInt(this.api.filtreDevelopments.priceMin, 10);
      integer2  = parseInt(value.priceT , 10);

      if(this.api.filtreDevelopments.priceMin  && value.priceT ){
        if(!( integer <= integer2 )){
          return;
        }
      }
      //-

      //Precio maximo
      integer   = parseInt(this.api.filtreDevelopments.priceMax, 10);
      integer2  = parseInt(value.priceF  , 10);

      if(this.api.filtreDevelopments.priceMax && value.priceF){
        if(!( integer >= integer2 )){
          return;
        }
      }
      //-

      //Recamaras minimas
      integer   = parseInt(this.api.filtreDevelopments.bedMin, 10);
      integer2  = parseInt(value.bedTo , 10);
      if(this.api.filtreDevelopments.bedMin && value.bedTo){
        if(!(integer <= integer2 )){
          return;
        }
      }
      //-

      //Recamaras maximas
      integer   = parseInt(this.api.filtreDevelopments.bedMax, 10);
      integer2  = parseInt(value.bedFrom  , 10);

      if(this.api.filtreDevelopments.bedMax && value.bedFrom){
        if(!(integer >= integer2 )){
          return;
        }
      }
      //-

      //Metros 2 minimos
      integer   = parseInt(this.api.filtreDevelopments.m2Min, 10);
      integer2  = parseInt(value.m2To, 10);

      if(this.api.filtreDevelopments.m2Min && value.m2To){

        if(!(integer <= integer2)){
          return;
        }
      }
      //-

      //Metros 2 Maximos
      integer   = parseInt(this.api.filtreDevelopments.m2Max, 10);
      integer2  = parseInt(value.m2From, 10);

      if(this.api.filtreDevelopments.m2Max && value.m2From){
     // ////console.log(integer2 ,'--', integer, value.developmentNameEs);

        if(!(integer >= integer2)){
          return;
        }
      }
      //-

      //Cajones estacionamientos maximas
      integer   = parseInt(this.api.filtreDevelopments.parkMax, 10);
      integer2  = parseInt(value.parkingF  , 10);
      
      if(this.api.filtreDevelopments.parkMax && value.parkingF){
        if(!(integer >= integer2)){
          return;
        }
      }
      //-

      //Cajones estacionamientos minimos
      integer   = parseInt(this.api.filtreDevelopments.parkMin, 10);
      integer2  = parseInt(value.parkingT, 10);

      if(this.api.filtreDevelopments.parkMin && value.parkingT){
        if(!(integer <= integer2)){
          return;
        }
      }


      //-

      if(type){
        if (value.stateid == type) {
          $this.cardItems.push(value);
        }else{
          return;
        }
      } else {
        $this.cardItems.push(value);

      }

    });
  }


  filtre(){
    console.log('filtre')

    this.navCtrl.push('FiltrePage', {page : 'developments'} );
  }
  
}



